<template>

  <div class="playlist" v-if="listdata">
    <div class="playlist-top" v-if="musicData.length>1">
      <div
        class="bgimage"
        :style="{
          background: `url(${listdata.coverImgUrl})`,
          backgroundPosition: ' 50% 35%',
        }"
      ></div>
      <div class="top-left">
        <img :src="listdata.coverImgUrl" alt="" />
        <div class="songs-manu">歌单</div>
        <div class="playCount">{{ listdata.playCount | people }}</div>
      </div>
      <div class="top-right">
        <div class="say">
          {{ listdata.name }}
        </div>
        <div class="artor">
          <img class="img-artor" :src="picUrl" />
          <span>{{ artor }}</span>
        </div>
      </div>
    </div>
    <div class="nextlist" v-if="musicData.length > 1">歌曲列表</div>
    <ul>
      <li v-for="(item, index) in musicData" :key="item.id">
        <div class="number" :style="{ color: '#999' }">
          {{ (index + 1) | filterIndex }}
        </div>
        <music-list class="number-song"  @play="$emit('play',$event)" :songIfa="item"> </music-list>
      </li>
    </ul>
    <hot-comment :hots="hotComment" v-if="musicData.length > 1"></hot-comment>
    <div>
      <div class="nextlist" v-if="musicData.length > 1">最新评论</div>
       <ul>
      <li v-for="(item,index) in newComment" :key="index">
          <new-comment :news="item" v-if="musicData.length > 1"></new-comment>
      </li>
    </ul>
  
    </div>
   
  </div>
</template>

<script>
import MusicList from "../components/MusicList.vue";
import HotComment from "../components/HotComment.vue";
import NewComment from "../components/NewComment.vue";
export default {
  data() {
    return {
      listId: this.$route.params.listId,
      listdata: {},
      musicData: [],
      br: [],
      picUrl: "",
      hotComment: [],
      newComment: [],
      artor:""
    };
  },
  methods: {
    async MusicList() {
      let re2 = await this.axios.get(
        `/comment/playlist?id=${this.$route.params.listId}&limit=10`
      );
      let re1 = await this.axios.get(
        `/playlist/detail?id=${this.$route.params.listId}`
      );
 
      this.listdata = re1.data.playlist;
      this.picUrl = this.listdata.creator.avatarUrl;
      this.musicData = re1.data.playlist.tracks;
      this.br = re1.data.privileges;
      this.artor=this.listdata.creator.nickname;


      this.hotComment = re2.data.hotComments;
      this.newComment = re2.data.comments;


      for (let i = 0; i < this.musicData.length; i++) {
        this.musicData[i]["maxbr"] = this.br[i].maxbr;
      }
    },
  },
  created() {
    this.MusicList();
  },
  components: {
    MusicList,
    HotComment,
    NewComment,

  },
};
</script>

<style lang="less" scoped>
.playlist {
  position: relative;
  .playlist-top {
    // width: 100%;
    padding: 30px 10px 30px 15px;
    display: flex;
    .bgimage {
      position: absolute;
      z-index:-5;
      top: 0;
      left: 0;
      width: 100%;
      height: 190px;
      filter:blur(10px);
    }
    &::after {
      content: " ";
      background-color: rgba(0, 0, 0, 0.5);
      width: 100%;
      height: 190px;
      position: absolute;
      top: 0px;
      left: 0;
      z-index: -3;
    }
    .top-left {
      z-index: -1;
      position: relative;
      img {
        width: 126px;
      }
      .songs-manu {
        position: absolute;
        top: 10px;
        left: 0;
        width: 40px;
        line-height: 17px;
        color: white;
        font-size: 12px;
        background-color: rgb(186, 51, 55);
        border-bottom-right-radius: 8px;
        border-top-right-radius: 8px;
      }
      .playCount {
        padding: 3px;
        position: absolute;
        right: 0;
        top: 0;
        
        font-size: 11px;
        color: #fff;
        &::before {
          content: "";
          position: absolute;
          background-image: url(data:image/svg+xml;base64,);
          background-repeat: no-repeat;
          width: 11px;
          height: 11px;
          top: 7px;
          left: -12px;
        }
      }
    }
    .top-right {
      padding-left: 20px;
      z-index: 10;
      text-align: left;
      flex: 1;
      .say {
        font-size: 17px;
        color: white;
      }
      .artor {
        padding:20px 0;
        display: flex;
        .img-artor {
          width: 30px;
          border-radius: 50%;
        }
        span {
          display: block;
          padding-left: 5px;
          font-size: 14px;
          height: 30px;
          line-height: 30px;
          color: rgb(184, 185, 191);
        }
      }
    }
  }
  .nextlist {
    padding: 3px 10px;
    font-size: 12px;
    color: rgb(102, 102, 102);
    text-align: left;
    background-color: rgb(238, 239, 240);
  }
  ul {
    li {
      display: flex;
      .number {
        width: 40px;
        line-height: 54px;
      }
      .number-song {
        flex: 1;
      }
    }
  }
  div{
      .nextlist {
    padding: 3px 10px;
    font-size: 12px;
    color: rgb(102, 102, 102);
    text-align: left;
    background-color: rgb(238, 239, 240);
  
    }
  }
}
</style>